export default "import React from 'react'\nimport path from 'path'\n\nimport DemoVanillaIframe from 'components/demo-vanilla-iframe'\n\nconst dirs = path.dirname(__filename).split('/')\nexport const demo = {\n  full: false,\n  name: path.basename(__filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop()\n}\n\ndemo.htmlSource = `\n<div class=\"sticky-0\"\n     data-xt-sticky>\n  <div class=\"card card--squared card--dark\">\n    <div class=\"card_design\"></div>\n    <div class=\"card_content\">\n      Sticky top\n    </div>\n  </div>\n</div>\n\n<div class=\"card card--light\">\n  <div class=\"card_design\"></div>\n  <div class=\"card_content\">\n    <h3>Lorem ipsum dolor sit amet</h3>\n    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin molestie diam nec euismod commodo. Nunc ut fringilla nibh. Duis quis arcu quis neque tempor lobortis nec nec mauris. Proin vel elit pretium metus egestas congue. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae; Quisque hendrerit sagittis quam eget elementum. Vestibulum eu nulla nisl. Duis nec commodo tortor. Aenean feugiat, libero eget ultricies viverra, justo nunc efficitur lorem, at aliquet ante eros in est.</p>\n  </div>\n</div>\n\n<div class=\"sticky-1\"\n     data-xt-sticky=\"{ contain: { top: '.sticky-0:not(.xt-clone)' } }\">\n  <div class=\"card card--squared card--light\">\n    <div class=\"card_design\"></div>\n    <div class=\"card_content\">\n      Sticky middle\n    </div>\n  </div>\n</div>\n\n<div class=\"card card--light\">\n  <div class=\"card_design\"></div>\n  <div class=\"card_content\">\n    <h3>Lorem ipsum dolor sit amet</h3>\n    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin molestie diam nec euismod commodo. Nunc ut fringilla nibh. Duis quis arcu quis neque tempor lobortis nec nec mauris. Proin vel elit pretium metus egestas congue. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae; Quisque hendrerit sagittis quam eget elementum. Vestibulum eu nulla nisl. Duis nec commodo tortor. Aenean feugiat, libero eget ultricies viverra, justo nunc efficitur lorem, at aliquet ante eros in est.</p>\n  </div>\n</div>\n\n<div class=\"sticky-2\"\n     data-xt-sticky=\"{ contain: { top: '.sticky-0:not(.xt-clone), .sticky-1:not(.xt-clone)' } }\">\n  <div class=\"card card--squared card--light\">\n    <div class=\"card_design\"></div>\n    <div class=\"card_content\">\n      Sticky bottom\n    </div>\n  </div>\n</div>\n\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n`\n\nclass Page extends React.Component {\n  render () {\n    return (\n      <DemoVanillaIframe demo={demo}/>\n    )\n  }\n}\n\nexport default Page\n"