export default "import path from 'path'\n\nconst dirs = path.dirname(__filename).split('/')\nexport const demo = {\n  name: path.basename(__filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop()\n}\n\ndemo.htmlSource = `\n<div data-xt-toggle=\"{ elements: '.radio', min: 1 }\">\n  <div class=\"form-group--inline\">\n    <div class=\"form-group\">\n      <button type=\"button\" class=\"checkbox active\" data-xt-toggle=\"{ elements: false }\">\n        Lorem ipsum dolor\n      </button>\n    </div>\n\n    <div class=\"form-group\">\n      <button type=\"button\" class=\"radio active\">\n        Lorem ipsum dolor\n      </button>\n    </div>\n\n    <div class=\"form-group\">\n      <button type=\"button\" class=\"radio\">\n        Lorem ipsum dolor\n      </button>\n    </div>\n  </div>\n</div>\n`\n"