export default "import path from 'path'\n\nconst dirs = path.dirname(__filename).split('/')\nexport const demo = {\n  name: path.basename(__filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop()\n}\n\ndemo.htmlSource = `\n  <div class=\"list list-space--small align-items--center\" data-xt-toggle>\n    <button type=\"button\" class=\"btn\">\n      Toggle 0\n    </button>\n    <button type=\"button\" class=\"btn\">\n      Toggle 1\n    </button>\n    <button type=\"button\" class=\"btn\">\n      Toggle 2\n    </button>\n    <div class=\"alert toggle--block\">\n      <div class=\"alert_content\">\n        Target 0\n        <div class=\"alert toggle--block\">\n          <div class=\"alert_content\">\n            Nested not toggled\n          </div>\n        </div>\n      </div>\n    </div>\n    <div class=\"alert toggle--block\">\n      <div class=\"alert_content\">\n        Target 1\n        <div class=\"alert toggle--block\">\n          <div class=\"alert_content\">\n            Nested not toggled\n          </div>\n        </div>\n      </div>\n    </div>\n    <div class=\"alert toggle--block\">\n      <div class=\"alert_content\">\n        Target 2\n        <div class=\"alert toggle--block\">\n          <div class=\"alert_content\">\n            Nested not toggled\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n`\n"