export default "import path from 'path'\n\nconst dirs = path.dirname(__filename).split('/')\nexport const demo = {\n  name: path.basename(__filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop()\n}\n\ndemo.htmlSource = `\n<form>\n  <div class=\"form-group\">\n    <label class=\"form-label\">\n      Input\n    </label>\n    <input type=\"text\" class=\"form-item\" placeholder=\"Input\">\n  </div>\n  \n  <div class=\"form-group\">\n    <label class=\"form-label\">\n      File\n    </label>\n    <input type=\"file\" class=\"form-item\">\n  </div>\n  \n  <div class=\"form-group\">\n    <label class=\"form-label\">\n      Textarea\n    </label>\n    <textarea class=\"form-item\" placeholder=\"Textarea\"></textarea>\n  </div>\n  \n  <div class=\"form-group\">\n    <label class=\"form-label\">\n      Select\n    </label>\n    <select class=\"form-item\">\n      <option>Test</option>\n      <option>Test</option>\n      <option>Test</option>\n    </select>\n  </div>\n  \n  <div class=\"form-group\">\n    <label class=\"form-label\">\n      Select multiple\n    </label>\n    <select class=\"form-item\" multiple>\n      <option>Test</option>\n      <option>Test</option>\n      <option>Test</option>\n    </select>\n  </div>\n  \n  <div class=\"form-group\">\n    <button type=\"submit\" class=\"btn btn--primary btn--sm\">\n      Submit\n    </button>\n  </div>\n</form>\n`\n"