export default "import path from 'path'\n\nconst dirs = path.dirname(__filename).split('/')\nexport const demo = {\n  name: path.basename(__filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop()\n}\n\ndemo.htmlSource = `\n<div class=\"list list-space--small align-items--center\"\n     data-xt-toggle=\"{ delayOn: 500, delayOff: 500 }\">\n  <button type=\"button\" class=\"btn\">\n    Toggle 0\n  </button>\n  <button type=\"button\" class=\"btn\">\n    Toggle 1\n  </button>\n  <button type=\"button\" class=\"btn\">\n    Toggle 2\n  </button>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 0\n    </div>\n  </div>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 1\n    </div>\n  </div>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 2\n    </div>\n  </div>\n</div>\n\n<br>\n\n<div class=\"list list-space--small align-items--center\"\n     data-xt-toggle=\"{ on: 'mouseenter', off: 'mouseleave', delayOn: 500, delayOff: 500 }\">\n  <button type=\"button\" class=\"btn\">\n    Toggle 0\n  </button>\n  <button type=\"button\" class=\"btn\">\n    Toggle 1\n  </button>\n  <button type=\"button\" class=\"btn\">\n    Toggle 2\n  </button>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 0\n    </div>\n  </div>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 1\n    </div>\n  </div>\n  <div class=\"alert toggle--block\">\n    <div class=\"alert_content\">\n      Target 2\n    </div>\n  </div>\n</div>\n`\n"